import React, {Component} from 'react';

class Agreement extends Component {
	render () {
        return (
		<>
            <div id="introcon">
                <div id="introinfo" class="lessgapchildren">
                    <h2>Information</h2>
                    <hr/>
                    <p>*You can check for updates at <a href="https://wearedevs.net/" target="_blank" rel="noopener noreferrer">https://wearedevs.net/</a>. If anything is broken, please report it on the website's forum! You may need to disable firewalls/antiviruses to use this program. Due to the nature of exploitation, JJSploit will be incorrectly flagged as malicious. Failing to disable firewalls may break the software, to which you would need to reinstall.</p>
                    <p>*We do not act with any abusive experience. There are no crypto miners, no adware, and no viruses. We only ask that you check out the two YouTube channels which you will see after this.</p>
                    <p>*Feel free to show this off on YouTube! No permission is required.</p>

                    <h2>Privacy</h2>
                    <hr/>
                    <p>Data is collected, but your identity is anonymous. We do not collect any personally identifying information. We only collect information such as error events and what commands are used. Information is used only to improve user experience. Additional network requests are made such as updates.</p>

                    <h2>Advertisements</h2>
                    <hr/>
                    <p>Advertisements may be found throughout the interface.</p>
                    <p>If you purchase a product through a link from this interface, we may receive a share of the purchase. These are known as affiliate links.</p>
                </div>
                <button onClick={this.Agree}>Understood</button>
            </div>

            <style>{`
            #introcon{
                padding: 14px;
                display: flex;
                flex-direction: column;
                height: 100%;
            }

            #introinfo{
                flex: 1;
                overflow: auto;
                font-size: 13px;
            }

            #introcon button{
                margin: auto;
                margin-top: 12px;
                padding: 8px 12px;
                width: 250px;
                display: block;
                border: none;
                background-color: #4CAF50;
                border-radius: var(--less-round);
                color: white;
                font-weight: 400;
                font-size: 16px;
            }
            `}</style>
		</>
        )
    };

    Agree = () => {
        global.ipcRenderer.send('message', {action:"agreementagree"})
    }
}

export default Agreement