import React, {Component} from 'react';
import {connect} from 'react-redux';

import Info from './info'
import Attach from './attach'
import General from './general'
import Jailbreak from './jailbreak'
import BeeSimulator from './beesim'

class FormHandler extends Component {
	render () {
        switch(this.props.form){
            case "Agreement":
                return <Info/>
            case "Attach":
                return <Attach/>;
            case "General":
                return <General/>;
            case "Jailbreak":
                return <Jailbreak/>;
            case "Bee Simulator":
                return <BeeSimulator/>;
            default:
                return <Attach/>;
                //return <General/>; //Just for dev so I can skip past the inject page
        }
    };
}

function mapStateToProps (state) {
    const { form } = state.FormHandler
    return { form }
}

export default connect(mapStateToProps)(FormHandler)