import React, {Component} from 'react';
import {connect} from 'react-redux'

class Header extends Component {
	render () {
        var styles = {
            topMost: {
                backgroundColor: this.props.topMost ? "#27ae60" : "rgba(255,255,255,0.1)"
            }
        }

        return (
		<nav id="windowTopBar">
            <h1>
                JJSploit v{this.props.version} by 
                <a 
                id="site" 
                href="https://wearedevs.net/" 
                target="_blank" 
                rel="noopener noreferrer"> wearedevs.net</a>
            </h1>
            <div id="actions">
                <button id="fixroblox" onClick={this.FixRoblox}>Fix Roblox</button>
                <button id="alwaysontop" style={styles.topMost} onClick={this.ToggleTopMost}>Top Most</button>
                <button id="exit" onClick={this.ExitApp}>X</button>
            </div>
            <style>{`
            nav{
                background-color: #131418;
                display: flex;
                align-items: center;
                z-index: 2;
                -webkit-app-region: drag
            }

            h1{
                font-weight: 400;
                margin: 0;
                padding: 4px;
                font-size: 14px;
                font-weight: 300;
            }

            #site{
                -webkit-app-region: no-drag;
                text-decoration: none;
                font-size: 14px;
                cursor: pointer;
            }

            #site:hover{
                color: #3498DB;
            }

            #actions{
                display: flex;
                margin-left: auto;
            }

            #actions button{
                border: 0;
                margin-left: 2px;
                background-color: rgba(255,255,255,0.1);
                -webkit-app-region: no-drag;
            }

            #fixroblox{
                cursor: pointer;
                border: 0;
                padding: 4px;
                font-weight: 500;
            }

            #fixroblox:hover{
                background-color: rgba(255,255,255,0.2);
            }

            #actions #alwaysontop{
                cursor: pointer;
                border: 0;
                padding: 4px;
                background-color: #27AE60;
                font-weight: 500;
            }

            #actions #alwaysontop:hover{
                color: #ECF0F1;
            }

            #exit{
                background-color: transparent;
                cursor: pointer;
                width: 30px;
                height: 30px;
                text-align: center;
                line-height: 200%;
            }

            #exit:hover{
                background-color: red;
                color: white;
            }
            `}</style>
		</nav>
        )
    };
    
    ToggleTopMost = () => {
        global.ipcRenderer.send('message', {action:"toggletopwindow"})
    }

    FixRoblox = () => {
        global.ipcRenderer.send('message', {action:"fixroblox"})
    }

    ExitApp = () => {
        global.ipcRenderer.send('message', {action:"exitapp"})
    }

    OpenWebsite = () => {
        window.open("https://wearedevs.net", "_blank")
    }
}

function mapStateToProps (state) {
    const { topMost, version } = state.Settings
    return { topMost, version }
}

export default connect(mapStateToProps)(Header)