import React, {Component} from 'react';
import {editor} from 'monaco-editor';

import GeneralNav from './nav'

class LuaCExecuter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            script: ""
        };

        this.monacoEditor = null
        this.fileInput = React.createRef();
    }

    componentDidMount(){
        this.monacoEditor = editor.create(document.getElementById('codecontainer'), {
            value: [
                'getglobal print',
                'pushstring Hello world!',
                'pcall 1 0 0',
            ].join('\n'),
            minimap: {
                enabled: false
            },
            scrollbar: {
                horizontal: "hidden",
                verticalScrollbarSize: 5
            },
            //lineNumbersMinChars: 2, //monaco-editor@0.17.0 breaks fold button when this line < 5
            showFoldingControls: "always",
        });

		global.ipcRenderer.on('luainput', this.InputFile)
    }

    componentWillUnmount(){
        this.monacoEditor.dispose()

		global.ipcRenderer.removeListener('luainput', this.InputFile)
    }

	render () {
        return (
		<>
            <div id="luaexecutor">
                <GeneralNav/>
                <div id="codecontainer"></div>
                <div id="luaButtons">
                    <input className="luaButton" type="submit" value="Execute" onClick={this.SendScript}/>
                    <input ref={this.fileInput} type="file" accept=".txt,.lua" style={{display: "none"}} onChange={this.OpenFile}/>
                    <input className="luaButton" type="button" value="Open File" 
                    onClick={this.TriggerFileInputClick}
                    onDrop={this.Drop}
                    onDragOver={this.onDragOver}/>
                </div>
            </div>

            <style>{`
            #luaexecutor{
                height: 100%;
                display: flex;
                flex-direction: column;
            }

            #codecontainer{
                flex: 1;
            }

            #luaButtons{
                display: flex;
            }

            .luaButton{
                width: 120px; 
                margin: auto; 
                margin-bottom: 5px; 
                border: none; 
                padding: 3px 5px; 
                background-color: #131418; 
                color: #fff;
                background: #3ecf8e;
                text-shadow: 0 1px 3px rgba(36,180,126,.4);
                border-radius: var(--less-round);
                padding: 8px 14px;
                margin-top: 10px;
            }
            `}</style>
		</>
        )
    };

    SendScript = () => {
        var script = editor.getModels()[0].getValue()
        global.ipcRenderer.send('message', {action: "luacscript", script: script})
    }

    TriggerFileInputClick = e => {
		this.fileInput.current.click()
    }

    //The file input was used
    OpenFile = e => {
        if(e.target.files[0]){
            var filepath = e.target.files[0].path
            this.ReqestInputFile(filepath)
        }
        //Had to nullify so onChange will recognize a change
        e.target.value = null
    }

    //File was dragged onto the button
    Drop = (e) => {
        e.preventDefault();
        e.stopPropagation();
        
        for (let f of e.dataTransfer.files) {
            var extension = f.path.substr(f.path.length - 4)
            if(extension === ".txt" || extension === ".lua"){
                var filepath = f.path;
                this.ReqestInputFile(filepath)
                break
            }
        }
    }

    onDragOver = (e) => {
        e.preventDefault();
    }

    ReqestInputFile = (filePath) => {
        global.ipcRenderer.send('message', {action:"openluafile", filePath: filePath})
    }

    InputFile = (event, message) => {
        this.monacoEditor.setValue(message)
    }
}

export default LuaCExecuter