import React, {Component} from 'react';
import {connect} from 'react-redux'

import YTLogo from '../../assets/50x50.png'
import DiscordHeadWhite from '../../assets/discord-head-white.png'
import DomainIcon from '../../assets/domain.png'

class PromoButton extends Component {
    constructor(props){
        super(props)
        this.state = {
            icon: "#"
        }
    }

    componentWillMount(){
        switch(this.props.type){
            case "YouTube":
                this.setState({icon: YTLogo, bgColor: "#fe4c40"})
                break
            case "Discord":
                this.setState({icon: DiscordHeadWhite, bgColor: "#7289da"})
                break;
            case "Website":
                this.setState({icon: DomainIcon, bgColor: "#3498db"})
                break;
            default: break;
        }
    }

	render () {
        return (
            <>
                <a className="pomoButton less-round" style={{backgroundColor: this.state.bgColor}} href={this.props.link} target="_blank" rel="noopener noreferrer">
                    <img src={this.state.icon} alt=""/>
                    <p className="text-clouds">{this.props.text}</p>
                </a>
                <style>{`
                .pomoButton{
                    max-width: 180px;
                    width: 100%;
                    height: 45px;
                    background-color: #fe4c40;
                    text-align: center;
                    display: flex;
                    justify-content: center;
                    margin-right: 6px;
                    text-decoration: none;
                    display: flex;
                    align-items: center;
                    font-size: 14px;
                    padding: 6px 8px;
                }

                .pomoButton:last-of-type{
                    margin: 0;
                }

                .pomoButton img{
                    margin-right: 5px;
                    height: 25px;
                    width: 25px;
                    margin-right: 8px;
                }
                `}</style>
            </>
        )
    };
}

class Attach extends Component {
	render () {
        return (
		<>
            <div id="split1" className="section">
                <div id="attachcontainer">
                    <h1>You must attach JJSploit to a game first</h1>
                    <div className="attachMethods">
                        <button className="btnAttach less-round" onClick={this.Attach}>Attach {this.props.betaModuleFound && "Classic"}</button>
                        <button className="btnLegacyAttach" onClick={this.LegacyAttach} title="If the above is not working, try this. However, you may be detected and banned from the game.">Attach Unsafe</button>
                    </div>
                    <p id="ServerMessage">{this.props.serverMessage || "Loading server message..."}</p>
                </div>
                <div id="ytbuttons">
                    <PromoButton link="https://wearedevs.net/forum" text="Community" type="Website"/>
                    <PromoButton link="https://www.youtube.com/c/Omnidev_" text="Learn Code" type="YouTube"/>
                    <PromoButton link="https://www.youtube.com/channel/UC3YNONzSHPW12m3AT48fMHw" text="Exploit Tutorials" type="YouTube"/>
                </div>
            </div>
            <br/>
            <div className="section lessgapchildren">
                <p>Notes:</p>
                <p>*Use a <a href="https://www.xvinlink.com/?a_fid=wearedevs" target="_blank" rel="noopener noreferrer">VPN</a> if JJSploit is failing to connect to the internet for assets or updates.</p>
                <p><a href="https://wearedevs.net/forum/t/23563" target="_blank" rel="noopener noreferrer">*Disable your anti-virus/firewall</a> if JJSploit's important files are breaking or deleting! Due to the nature of exploits(Obfuscation, game manipulation, etc), they're falsely marked as viruses/malware.</p>
                <p>*<a href="https://aka.ms/vs/16/release/vc_redist.x86.exe" target="_blank" rel="noopener noreferrer">Download VC++ x86</a> if the DLL Injector is not working</p>
                <p>*If it says its patched, you must wait for the creators to update! This could be a few hours or a few days if there are serious complications.</p>
                <p>*You can ask for help at <a href="https://wearedevs.net/forum#Exploiting" target="_blank" rel="noopener noreferrer">https://wearedevs.net/forum</a></p>
            </div>
            <br/>
            <div id="split2" className="section">
                <p style={{textAlign: "center"}}>Powered by the <a href="https://wearedevs.net/d/Exploit%20API" target="_blank" rel="noopener noreferrer">WeAreDevs Exploit API</a></p>
            </div>

            <style>{`
            .section{
                padding: 12px;
            }

            #split1{
                height: 100%;
                display: flex;
                flex-direction: column;
            }

            #attachcontainer{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                flex: 1;
            }

            #split2 ul li{
                margin-left: 16px;
            }

            #split1 h1{
                text-align: center; 
                font-size: 22px;
                margin-bottom: 10px;
                padding: 0;
            }

            .btnAttach{
                max-width: 160px;
                width: 100%;
                height: 40px;
                border: none;
                background-color: #171717;
                cursor: pointer;
                font-size: 16px;
                padding: 12px 0;
            }

            .btnLegacyAttach{
                max-width: 160px;
                width: 100%;
                border: none;
                cursor: pointer;
                font-size: 12px;
                font-style: italic;
                background: 0;
            }

            #ServerMessage{
                text-align: center; 
                width: 100%; 
                max-height: 70px;
                overflow-y: auto;
                font-size: 15px;
                padding: 12px 0;
            }

            #ytbuttons{
                display: flex;
                justify-content: space-around;
                width: 100%;
            }
            `}</style>
		</>
        )
    };

    Attach = () => {
        global.ipcRenderer.send('message', {action:"attach"})
    }

    LegacyAttach = () => {
        global.ipcRenderer.send('message', {action:"legacyAttach"})
    }
}

function mapStateToProps (state) {
    const { topMost, serverMessage, betaModuleFound } = state.Settings
    return { topMost, serverMessage, betaModuleFound }
}

export default connect(mapStateToProps)(Attach)