import React, {Component} from 'react';
import {connect} from 'react-redux'

import {SET_FORM_VIEW} from '../../redux/actions'

class FormOption extends Component {
	render () {
        return (
		<div className="formOption" onClick={this.ChangeForm}>
            <p>{this.props.title}</p>

            <style>{`
            .formOption{
                text-align: center;
                cursor: pointer;
                margin: 5px 5px;
                font-size: 14px;
            }
            `}</style>
		</div>
        )
    };

    ChangeForm = () => {
        var {dispatch, title} = this.props
        dispatch(SET_FORM_VIEW(title));
    }
}

export default connect()(FormOption)