import React, {Component} from 'react';
import {connect} from 'react-redux'

import {SET_MESSAGE} from '../redux/actions'

class Message extends Component {
    render (){
        var styles = {
            messageBox: {
                display: (this.props.subject && (this.props.subject.length > 0)) ?  "flex" : "none"
            },
            error: {
                color: "red"
            },
        }

        return (
        <div id="msgBoxModal" style={styles.messageBox}>
            <div className="msgcontent">
                <p className="msgsubject" style={this.props.subject==="error" ? styles.error : {} }>{this.props.subject}</p>
                <p className="msgBoxText">{this.props.text}</p>
                <br/>
                <button onClick={this.HideMessage}>Okay</button>
            </div>

            <style>{`
            #msgBoxModal{
                height: 100vh;
                width: 100vw;
                background-color: rgba(0,0,0,0.5);
                flex-direction: column;
                justify-content: center;
                align-items: center;
                position: absolute;
                padding: 12px;
                z-index: 1;
            }

            .msgcontent{
                background-color: #333333;
                padding: 12px;
                min-width: 270px;
                border-radius: var(--less-round);
                box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
            }

            .msgsubject{
                margin-bottom: 12px;
                text-align: center;
            }

            .msgBoxText{
                text-align: center;
            }

            .msgcontent button{
                background-color: #171717;
                border: none;
                padding: 5px 32px;
                text-align: center;
                text-decoration: none;
                display: block;
                font-size: 15px;
                margin: auto;
                border-radius: var(--less-round);
            }
            `}</style>
        </div>
        );
    }

    HideMessage = () => {
        var {dispatch} = this.props
        dispatch(SET_MESSAGE({subject: null, text: null}))
    }
}

function mapStateToProps (state) {
    const { subject, text } = state.MessageBox
    return { subject, text }
}

export default connect(mapStateToProps)(Message);
