import React, {Component} from 'react';
import {connect} from 'react-redux'

import FormOption from './formoption'

class SideMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            menuMargin: -140,
            toggleRotation: 0
        };

        this.menu = React.createRef();
    }

    componentDidMount(){
        //Close menu
        this.closeMenu()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.form !== this.props.form) {
            //Form changed. Close the menu
            this.closeMenu()
        }
    }

	render () {
        return (
		<div ref={this.menu} id="SideMenu" style={{marginLeft: this.state.menuMargin}}>
            <div className="sideMenu_Toggle">
                <p onClick={this.toggleMenu}>{`>>`}</p>
            </div>
            <p className="sideMenu_title">Games</p>
            <hr/>
            <FormOption title="General"/>
            <FormOption title="Jailbreak"/>
            <FormOption title="Bee Simulator"/>

            <style>{`
            #SideMenu{
                width: 320px;
                max-width: 120px;
                border-right: 1px solid gray;
                background-color: white;
                position: fixed;
                height: 100%;
                box-shadow: 0px 0px 9px 0px #888888;
                z-index: 1;
                transition: all 0.3s ease-in-out;
            }

            .sideMenu_Toggle{
                margin-left: 100%;
                padding-left: 20px;
                position: absolute;
                height: 100%;
                display: flex;
                align-items: center;
                padding-bottom: 30px;
            }

            .sideMenu_Toggle p{
                background-color: white;
                border: 1px solid gray;
                padding: 10px 11px;
                border-radius: 100%;
                cursor: pointer;
                transition: all 0.3s ease-in-out;
                transform: rotate(${this.state.toggleRotation}deg);
                opacity: 0.5;
            }

            .sideMenu_title{
                color: gray;
                text-align: center;
                font-size: 14px;
                margin: 5px 0px;
            }
            `}</style>
		</div>
        )
    };

    toggleMenu = () => {
        if(this.state.menuMargin < 0){
            //Open menu
            this.setState({menuMargin: 0, toggleRotation: 180})
        }else{
            //Close menu
            this.closeMenu()
        }
    }

    closeMenu = () => {
        var width = this.menu.current.clientWidth
        var newRotation = 0
        var newMargin = (width+18) * -1
        this.setState({menuMargin: newMargin, toggleRotation: newRotation})
    }
}

function mapStateToProps (state) {
    const { form } = state.FormHandler
    return { form }
}

export default connect(mapStateToProps)(SideMenu)