import React, {Component} from 'react';
import {connect} from 'react-redux'

class CmdButton extends Component {
	render () {
        return (
            <button 
            className="quickcommand" 
            onClick={this.SendMessage}>
                <p>{this.props.text}</p>
               

            <style>{`
            .quickcommand{
				padding: 5px 10px;
				text-align: center;
				background-color: #131418;
				font-size: 14px;
				margin-top: 3px;
                cursor: pointer;
                width: 110px;
                color: white;
                margin: 5px;
                border: none;
            }
            `}</style>
		</button>
        )
    };

    SendMessage = () => {
        var action = Object.keys(this.props.message)[0] //hacky to keep the props passing short. it wouldve been nasty to specify the entire object 
        global.ipcRenderer.send('message', {action: action, data: this.props.message[action]})
    }
}

class SideMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }

	render () {
        return (
		<div style={{height: "100%"}}>
            <div id="quickcommands">
                <CmdButton text="Criminal ESP" message={{"storedluascript": {filename:'jailbreak/criminalesp.lua'}}}/>
                <CmdButton text="Police ESP" message={{"storedluascript": {filename:'jailbreak/policeesp.lua'}}}/>
                <div class="quickcommand" onClick={this.KeyCard}><p>Keycard</p></div>

                <CmdButton text="Noclip" message={{"storedluascript": {filename:'general/noclip.lua'}}}/>
                <div class="quickcommand" onClick={this.NoGravity}><p>Low gravity</p></div>
                <div class="quickcommand" onClick={this.Gravity}><p>Gravity</p></div>
            </div>

            <style>{`
            #quickcommands{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                height: 100%;
                align-items: center;
                max-width: 450px;
                margin: auto;
            }

            .quickcommand{
                padding: 5px 10px;
				text-align: center;
				background-color: #131418;
				font-size: 14px;
				margin-top: 3px;
                cursor: pointer;
                width: 110px;
                color: white;
                margin: 5px;
            }
            `}</style>
		</div>
        )
    };

    SendScript = (script) => {
        global.ipcRenderer.send('message', {action: "luascript", script: script})
    }

    KeyCard = () => {
        this.SendScript("game:GetService(\"Players\").LocalPlayer.TeamValue.Value = \"Police\"")
    }

    Gravity = () => {
        this.SendScript("game:GetService(\"Workspace\").Gravity = 196.2")
    }

    NoGravity = () => {
        this.SendScript("game:GetService(\"Workspace\").Gravity = 25")
    }
}

function mapStateToProps (state) {
    const { topMost } = state.Settings
    return { topMost }
}

export default connect(mapStateToProps)(SideMenu)