import React, {Component} from 'react';
import {connect} from 'react-redux'

import SideMenu from './sidemenu'
import FormHandler from './forms'

class ContentContainer extends Component {
    render(){
        var menuBlacklist = [undefined, "Attach", "KeyAccess", "Agreement"]
        var {form} = this.props

        return (
            <div id="container">
                {/* Hide the menu from some forms */}
                {/*menuBlacklist.indexOf(form)===-1 && <SideMenu/>*/}
                <FormHandler/>
                
                <style>{`
                #container{
                    flex: 1;
                    flex-direction: column;
                    overflow-y: auto;
                    overflow-x: hidden;
                    position: relative;
                    background-color: #333333;
                }
                `}</style>
            </div>
        );
    }
}

function mapStateToProps (state) {
    const { form } = state.FormHandler
    return { form }
}

export default connect(mapStateToProps)(ContentContainer)