function Settings(state = [], action) {
    switch (action.type) {
        case 'SET_SETTING':
            return Object.assign({}, state, action.settings)
        case 'SET_SERVER_MESSAGE':
            return Object.assign({}, state, action.serverMessage)
        case 'DISPLAY_BETA_INJECT':
            return Object.assign({}, state, action.betaModuleFound)
        default:
            return state
    }
}

export default Settings