import React, {Component} from 'react';
import {connect} from 'react-redux'

import {SET_TAB_VIEW} from '../../../redux/actions'

class GeneralNav extends Component {
	render () {
        return (
		<div id="generalnav">
            <p className="generalnavtab" onClick={this.setTab_Buttons}>Buttons</p>
            <p className="generalnavtab" onClick={this.setTab_Lua}>Lua</p>
            {/*<p className="generalnavtab" onClick={this.setTab_LuaC}>Lua C</p>*/}

            <style>{`
            #generalnav{
                display: flex;
            }

            .generalnavtab{
                flex: 1;
				text-align: center;
				background-color: #171717;
				-webkit-transition: background-color 1000ms linear;
				-ms-transition: background-color 300ms linear;
				transition: background-color 300ms linear;
				border-bottom: 1px solid #131418;
				padding: 5px 10px;
				font-weight: 400px;
                text-decoration: none;
                cursor: pointer;
            }

            .generalnavtab:nth-child(2){
                border-left: 1px solid black;
                /*border-right: 1px solid black;*/
            }
            `}</style>
		</div>
        )
    };

    setTab_Buttons = () => {
        var {dispatch} = this.props
        dispatch(SET_TAB_VIEW("buttons"))
    }

    setTab_Lua = () => {
        var {dispatch} = this.props
        dispatch(SET_TAB_VIEW("luaexecutor"))
    }

    setTab_LuaC = () => {
        var {dispatch} = this.props
        dispatch(SET_TAB_VIEW("luacexecutor"))
    }
}

function mapStateToProps (state) {
    const { tab } = state.GeneralForm
    return { tab }
}

export default connect(mapStateToProps)(GeneralNav)