/* Settings */

//settings param should be an object
export const SET_SETTING = settings => ({
    type: 'SET_SETTING', 
    settings
}) 

//settings param should be an object
export const SET_SERVER_MESSAGE = serverMessage => ({
    type: 'SET_SERVER_MESSAGE', 
    serverMessage
}) 

export const DISPLAY_BETA_INJECT = betaModuleFound => ({
    type: 'DISPLAY_BETA_INJECT', 
    betaModuleFound
}) 

/* Form Handler */
export const SET_FORM_VIEW = form => ({
    type: 'SET_FORM_VIEW', 
    form
}) 

/* MessageBox */

//Change the message box text
export const SET_MESSAGE = message => ({
    type: 'SET_MESSAGE', 
    message
}) 

/* GeneralForm */

//tab param should be a string
export const SET_TAB_VIEW = tab => ({
    type: 'SET_TAB_VIEW', 
    tab
}) 

/* Lua Executor Form */

//tab param should be a string
export const SET_LASTLUASCRIPT = lastLuaScript => ({
    type: 'SET_LASTLUASCRIPT', 
    lastLuaScript
}) 