import React, {Component} from 'react';
import {connect} from 'react-redux';

import Message from './components/message'
import Header from './components/header';
import ContentContainer from './components/contentcontainer';

import {SET_SETTING, SET_SERVER_MESSAGE, DISPLAY_BETA_INJECT, SET_MESSAGE, SET_FORM_VIEW} from './redux/actions'

class App extends Component{
	componentDidMount () {
		// start listening the channel message
		global.ipcRenderer.on('message', this.handleMessage)
	}

	componentWillUnmount () {
		// stop listening the channel message
		global.ipcRenderer.removeListener('message', this.handleMessage)
	}

	render(){
		return (
			<div style={{height: "100vh", display: "flex", flexDirection: "column"}}>
				<Message/>
				<Header/>
				<ContentContainer/>
			</div>
		);
	}
	
	handleMessage = (event, message) => {
		var {dispatch} = this.props

		if(typeof message === "string"){
			switch(message){
				case "hideMessageBox":
					dispatch(SET_MESSAGE({subject: null, text: null}))
					break
				default:
					break;
			}
		}
		else if(typeof message === "object"){
			if("topMost" in message){
				dispatch(SET_SETTING({topMost: message.topMost}))
			}
			else if("version" in message){
				dispatch(SET_SETTING({version: message.version}))
			}
			else if("showMessageBox" in message){ //Show message box
				dispatch(SET_MESSAGE(message.showMessageBox))
			}
			else if("serverMessage" in message){
				dispatch(SET_SERVER_MESSAGE({serverMessage: message.serverMessage}))
			}
			else if("betaModuleFound" in message){
				dispatch(DISPLAY_BETA_INJECT({betaModuleFound: message.betaModuleFound}))
			}
			else if("changePage" in message){
				dispatch(SET_FORM_VIEW(message.changePage))
			}
		}
	}
}

export default connect()(App);