import React, {Component} from 'react';
import {connect} from 'react-redux'

class CmdButton extends Component {
	render () {
        return (
            <button 
            className="quickcommand" 
            onClick={this.SendMessage}>
                <p>{this.props.text}</p>
               

            <style>{`
            .quickcommand{
				padding: 5px 10px;
				text-align: center;
				background-color: #131418;
				font-size: 14px;
				margin-top: 3px;
                cursor: pointer;
                width: 110px;
                color: white;
                margin: 5px;
                border: none;
            }
            `}</style>
		</button>
        )
    };

    SendMessage = () => {
        var action = Object.keys(this.props.message)[0] //hacky to keep the props passing short. it wouldve been nasty to specify the entire object 
        global.ipcRenderer.send('message', {action: action, data: this.props.message[action]})
    }
}

