class SideMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }

	render () {
        return (
		<div style={{height: "100%"}}>
            <div>
                <div className="section quickcommands">
                    <CmdButton text="Auto Dig" message={{"storedluascript": {filename:'beesim/autodig.lua'}}}/>
                </div>
                <br/>
                <div id="specgencoms" className="section quickcommands">
                    <Teleports/>
                </div>
                <br/>
                <div className="section quickcommands">
                    <CmdButton text="TP Tool" message={{"storedluascript": {filename:'general/tptool.lua'}}}/>
                    <CmdButton text="Infinite Jump" message={{"storedluascript": {filename:'general/infinitejump.lua'}}}/>
                    <button className="quickcommand" onClick={this.NoGravity}><p></p>Low gravity</button>
                    <button className="quickcommand" onClick={this.Gravity}>Gravity</button>
                </div>
            </div>

            <style>{`
            #quickcommands{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                height: 100%;
                align-items: center;
                max-width: 450px;
                margin: auto;
            }

            .quickcommands{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
            }

            .quickcommand{
                padding: 5px 10px;
				text-align: center;
				background-color: #131418;
				font-size: 14px;
				margin-top: 3px;
                cursor: pointer;
                width: 110px;
                color: white;
                margin: 5px;
            }

            .section{
                padding: 12px;
            }

            #specgencoms{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
            }

            #specgencoms > div{
                margin: 25px;
                width: 230px;
            }

            #specgencoms > div > p:nth-child(1){
                color: gray;
                margin-bottom: 5px;
            }

            #specgencoms input, #specgencoms select{
                border: 1px solid #ccc;
                padding: 8px;
                width: 100%;
                max-width: 180px;
                margin-right: 5px;
            }

            #specgencoms button{
                border: 1px solid #ccc;
                padding: 8px;
            }
            `}</style>
		</div>
        )
    };

    SendScript = (script) => {
        global.ipcRenderer.send('message', {action: "luascript", script: script})
    }

    KeyCard = () => {
        this.SendScript("game:GetService(\"Players\").LocalPlayer.TeamValue.Value = \"Police\"")
    }

    Gravity = () => {
        this.SendScript("game:GetService(\"Workspace\").Gravity = 196.2")
    }

    NoGravity = () => {
        this.SendScript("game:GetService(\"Workspace\").Gravity = 25")
    }
}

function mapStateToProps (state) {
    const { topMost } = state.Settings
    return { topMost }
}

export default connect(mapStateToProps)(SideMenu)